.TarotGame {
    width: calc(100vw - 100px);
}

.welcome {
    background-image: url('../../public/Crystal-Ball.jpg');
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    background-color: #010714;
    top: 0;
    left: 0;
    height: 80vh;
    width: 100%;
    cursor: pointer;
    position: relative;
    z-index: 1;
}

@media (max-width: 640px) {
    .welcome {
        height: 40vh;
    }
}

.readingContainer.grid {
    margin: 16px auto 32px;
    width: auto;
    max-width: 1024px;
}

.tarot-container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
    width: calc(100% - 60px);
    max-width: 1700px;
    margin: 0 0 0 60px;
}

.tarot-v1 .title {
    font-size: 2.3rem;
}

@media (max-width: 960px) {
    .tarot-container {
        flex-direction: column;
    }
}

.tarot-container .tarot-card {
    position: static;
}

.tarot-item {
    width: 30%;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    margin: 10px 0;
    color: white;
    font-size: 1.2em;
    flex-direction: column;
}

@media (max-width: 960px) {
    .tarot-item {
        width: auto;
        margin: 30px;
    }
}

.gameName {
    margin: 0;
    position: absolute;
    left: calc(50% + 30px);
    transform: translateX(-50%);
}

.tarotHeading {
    font-size: 48px;
    margin: 12px;
    color: #717070;
    width: 100%;
}

.cardBackground.white {
    background-color: transparent;
    position: relative;
}

.daliTarotCard {
    position: relative;
    width: 100%;
    transform-style: preserve-3d;
    transition: all 0.8s;
    perspective: 1000px; /* Remove this if you don't want the 3D effect */
    z-index: 1;
    min-height: 42vh;
}

.daliTarotCard.flipped {
    transform: rotateY(180deg);
}

.daliTarotCard .daliTarotCardFront,
.daliTarotCard .daliTarotCardBack {
    position: absolute;
    -webkit-backface-visibility: hidden; /* Safari */
    backface-visibility: hidden;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    cursor: pointer;
}

.daliTarotCard .daliTarotCardFront {
    transform: rotateY(180deg) translateX(50%);
}

.daliTarotCard img {
    max-height: 100%;
    max-width: 100%;
    border-radius: 15px;
}

@media (max-width: 968px) {
    .daliTarotCard img {
        border-radius: 5px;
    }
}

.cardInfo {
    opacity: 0;
}

.cardInfo.flipped {
    opacity: 1;
}

.tarot-container .cardInfo {
    transform: translateY(calc(-100% - 36px));
    transition: transform 0.8s, opacity 0.8s;
    display: block;
}

@media (max-width: 968px) {
    .cardInfo {
        transform: none;
        transition: margin-top 0.8s, opacity 0.8s;
        /*margin-top: -160px;*/
        height: 0;
    }
}

.tarot-container .cardInfo.flipped {
    transform: translateY(0);
    display: block;
}

@media (max-width: 968px) {
    .cardInfo.flipped {
        /*margin-top: 0;*/
        height: auto;
    }
}

.cardName {
    color: var(--text-color);
    margin-bottom: 16px;
}

.cardDesc {
    text-align: left;
}

