/*    }*/
/*}*/

@media (max-width: 968px) {
    .cardDesc {
    /*    margin-right: 10px;*/
    /*    margin-bottom: 20px;*/
    }
}

.cardLearnMore {
    cursor: pointer;
    /*position: absolute;*/
    /*bottom: -36px;*/
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
    margin-top: 0;
    background-color: transparent;
    color: var(--menu-color);
    border: none;
    text-transform: uppercase;
    padding: 0;
}

@media (max-width: 968px) {
    .cardLearnMore {
        /*left: 50%;*/
        /*transform: translateX(-50%);*/
    }
}

button.button {
    background-color: var(--menu-color);
    color: var(--background-color);
    min-width: 30px;
    min-height: 30px;
    position: relative;
    margin: 60px auto;
    cursor: pointer;
    text-align: center;
    width: auto;
    padding: 20px 30px;
    border-radius: 15px;
    transition: opacity 0.8s;
    text-decoration: none;
    display: inline-flex;
    border: 0 none;
}

@media (max-width: 640px) {
    button.button {
        margin: 20px 3px;
    }
}

button.button:hover {
    background-color: #f3b414;
}


button.button.visible {
    opacity: 1;
}

button.button.show-all-cards {
    margin-right: 20px;
}

@media (max-width: 640px) {
    button.button.show-all-cards {
        margin-right: 0;
    }
}

/*#modal*/
.mymodal {
    position: absolute;
    top: 2%;
    left: 2%;
    right: 2%;
    bottom: 2%;
    border: 1px solid var(--menu-color);
    background: var(--background-color);
    overflow: auto;
    --webkit-overflow-scrolling: touch;
    border-radius: 4px;
    outline: none;
    padding: 20px;
    box-shadow: #fed05738 0 0 12px 15px
}

@media (max-width: 968px) {
    .mymodal {
        left: 2%;
        right: 2%;
    }
}

.myoverlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.75);
    z-index: 2;
}

.modal-content {
    display: flex;
    justify-content: space-evenly;
}

@media (max-width: 968px) {
    .modal-content {
        flex-direction: column-reverse;
    }
}

.graphic-content {
    /*width: 50%;*/
}

@media (max-width: 968px) {
    .graphic-content {
        margin: 0 auto;
    }
}

.graphic-content img {
    border-radius: 15px;
    max-height: 93vh;
}


.text-content {
    max-width: 50%;
    padding: 0 24px 24px 24px;
    font-size: 1.3em;
}

@media (max-width: 968px) {
    .text-content {
        padding-top: 24px;
        max-width: 100%;
    }

    .text-content b {
        color: var(--menu-color)
    }
}

.text-content h2 {
    margin-top: 0;
}

.text-content p {
    color: var(--text-color);
    margin-bottom: 16px;
    line-height: 2.3em;
}

.close-icon {
    cursor: pointer;
    position: absolute;
    top: 16px;
    right: 16px;
    color: white;
}