html {
    margin: 0;
    padding: 0;
    height: 100%;
}

body {
    /*background: rgb(50, 50, 50);*/
    margin: 0;
    padding: 0;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    height: 100%;
}


