#generator {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
}


#canvas {
    width: 100%;
    height: 90%;
    overflow: hidden;
    position: relative;
}


#controls {
    display: flex;
    flex-direction: column;
    background: rgb(20, 20, 20);
    flex: 1;
    align-items: center;
    color: rgb(200, 200, 200);
    justify-content: center;
    font-family: sans-serif;
    font-weight: bold;
    overflow: hidden;
}

#grayscale-toggle {
    display: flex;
    align-items: center;
    padding-top: 2px;
    order: 3;
}

#grayscale-toggle p {
    margin-right: 30px;
}

#grayscale-toggle input {
    margin-top: 4px;
}

.control-item {
    margin: 2px;
    margin-bottom: 4px;
}

input[type=range] {
    -webkit-appearance: none;
    appearance: none;
    width: 110px;
    height: 4px;
    background: rgb(200, 200, 200);
    outline: none;
}

input[type=range]::-webkit-slider-thumb {
    border: 2px solid rgb(30, 30, 30);
    height: 16px;
    width: 16px;
    border-radius: 50%;
    background: rgb(200, 200, 200);
    cursor: pointer;
    -webkit-appearance: none;
}

input[type=range]::-moz-slider-thumb {
    border: 2px solid rgb(30, 30, 30);
    height: 16px;
    width: 16px;
    border-radius: 50%;
    background: rgb(200, 200, 200);
    cursor: pointer;
    -webkit-appearance: none;
}

