#generate-button {
    margin-top: 8px;
    background: rgb(20, 20, 20);
    border: 1px solid rgb(200, 200, 200);
    color: rgb(200, 200, 200);
    border-radius: 5px;
    padding: 8px;
    font-size: .8em;
    font-weight: bold;
    outline: none;
    order: 4;
    -webkit-user-select: none;
    -moz-user-select: none;
}

.rotate-message-true {
    position: absolute;
    text-align: center;
    color: rgba(200, 200, 200);
    background: rgb(70, 70, 70, .5);
    width: 100%;
}

.rotate-message-false {
    display: none;
}


/* --- Loading Placeholder --- */
.loading-block {
    width: 100%;
    height: 100%;
    position: absolute;
}

.loading-block-true {
    background: rgb(30, 30, 30);
    display: flex;
    align-items: center;
    justify-content: center;
}

.loading-block-false {
    display: none;
}

#loading-image {
    padding-left: 20px;
    padding-right: 20px;
    background: rgb(50, 50, 50);
}

#loading-image h1 {
    font-family: sans-serif;
    color: rgb(200, 200, 200);
}



/* end */

@media (orientation: landscape) {
    #generator {
        flex-direction: row;
    }

    #canvas {
        height: 100%;
    }

    #controls {
        display: flex;
        flex-direction: column;
        background: rgb(20, 20, 20);
        flex: 1;
        align-items: center;
        color: rgb(200, 200, 200);
        justify-content: center;
        font-family: sans-serif;
        font-weight: bold;
        overflow: hidden;
        position:static;
        top: 0;
        transform: none;
    }

    #grayscale-toggle {
        display: flex;
        align-items: center;
        padding-top: 2px;
        order: 0;
    }

    #grayscale-toggle p {
        margin-right: 30px;
    }

    .control-item {
        margin: auto;
    }

    /*p {*/
    /*    margin: auto;*/
    /*    font-size: .8em;*/
    /*}*/

    #generate-button {
        margin-top: 8px;
        background: rgb(20, 20, 20);
        border: 1px solid rgb(200, 200, 200);
        color: rgb(200, 200, 200);
        border-radius: 5px;
        padding: 8px;
        font-size: .8em;
        font-weight: bold;
    }
}


@media (min-width: 420px) and (orientation: landscape) {
    /*p {*/
    /*    font-size: .9em;*/
    /*    margin-bottom: 5px;*/
    /*}*/

    #generate-button {
        font-size: .9em;
    }
}

@media (min-width: 420px) and (min-height: 350px) {

    #controls {
        justify-content: flex-start;
        align-items: flex-start;

    }

    .control-item {
        margin-top: 20px;
        margin-bottom: 20px;
    }


}

@media (min-width: 420px) and (min-height: 510px) {

    /*p {*/
    /*    font-size: 1.1em;*/
    /*    margin-bottom: 5px;*/
    /*}*/

    #generate-button {
        padding: 18px;
        margin-top: auto;
    }

    #grayscale-toggle input {
        margin-top: -2px;
    }
}

