@media (orientation: portrait) {

    #canvas {
        width: 100%;
        height: 85%;
    }

    #controls {
        position: absolute;
        flex-direction: row;
        flex-wrap: wrap;
        top: 100%;
        transform: translateY(-100%);
        padding-bottom: 6px;
    }

    .control-item {
        margin-bottom: 10px;
    }

    #grayscale-toggle {
        order: 3;
    }

    #generate-button {
        order: 4;
    }

}

@media (orientation: portrait) and (min-width: 500px) {
    #generate-button {
        margin-top: 14px;
    }
}
